"use client";

import { useEffect, useState } from "react";
import QRCode from "qrcode";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Image from "next/image";

export default function ScannerPage({ params }) {
  const { shopId } = params;
  const [qr, setQr] = useState("");

  useEffect(() => {
    const url = `${process.env.NEXT_PUBLIC_APP_URL}/claim?shopId=${shopId}`;

    QRCode.toDataURL(url, { width: 800, margin: 1 }).then(setQr);
  }, [shopId]);

  const handleDownload = () => {
    const a = document.createElement("a");
    a.href = qr;
    a.download = `shop-qr-${shopId}.png`;
    a.click();
  };

  const handlePrint = () => {
    const win = window.open("");
    win.document.write(`<img src="${qr}" style="width:350px;" />`);
    win.print();
  };

  return (
    <div className="min-h-screen flex justify-center items-center p-6">
      <Card className="w-full max-w-sm shadow-xl border-0 rounded-2xl backdrop-blur">
        
        {/* Header */}
        <CardHeader className="text-center pb-2">
          <CardTitle className="text-2xl font-bold tracking-tight">
            Shop QR Code
          </CardTitle>
          <p className="text-sm text-muted-foreground mt-1">
            Display this QR at your counter for customers to scan.
          </p>
        </CardHeader>

        {/* QR Section */}
        <CardContent className="flex flex-col items-center gap-6 pt-4">
          
          {qr && (
            <div className="p-4 rounded-xl bg-white border shadow-inner">
              <img
                src={qr}
                alt="Shop QR Code"
                className="w-48 h-48 rounded-md"
              />
            </div>
          )}

          {/* Buttons */}
          <div className="w-full flex flex-col gap-3">
            <Button
              onClick={handleDownload}
              className="w-full text-base py-2 font-medium hover:cursor-pointer"
            >
              Download QR
            </Button>

            <Button
              onClick={handlePrint}
              variant="secondary"
              className="w-full text-base py-2 font-medium hover:cursor-pointer"
            >
              Print QR
            </Button>
          </div>

          {/* Footer text */}
          <p className="text-center text-xs text-muted-foreground leading-relaxed">
            Customers can scan this to access your loyalty page and claim
            rewards by uploading payment screenshots.
          </p>
        </CardContent>
      </Card>
    </div>
  );
}
